import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DrinkSessionService } from '../drink-session.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage implements OnInit {

  constructor(
    private router: Router,
    private drinkSession: DrinkSessionService
  ) { }

  ngOnInit() {
    this.drinkSession.getCurrentDrinkList().then(
      currentDrinkList => {
        console.log(currentDrinkList);
        if (currentDrinkList != null) {
          this.router.navigate(['tabs/bill', { drinkList: JSON.stringify(currentDrinkList) }])
        }
      },
      err => {
        console.log(err)
      }
    )

  }

  startBill() {
    this.router.navigate(['tabs/bill']);
  }

}
